<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>
    <ul>
      <li>
        <a
          href="https://vuejs.org"
          target="_blank"
        >
          Core Docs
        </a>
      </li>
      <li>
        <a
          href="https://forum.vuejs.org"
          target="_blank"
        >
          Forum
        </a>
      </li>
      <li>
        <a
          href="https://chat.vuejs.org"
          target="_blank"
        >
          Community Chat
        </a>
      </li>
      <li>
        <a
          href="https://twitter.com/vuejs"
          target="_blank"
        >
          Twitter
        </a>
      </li>
      <br>
      <li>
        <a
          href="http://vuejs-templates.github.io/webpack/"
          target="_blank"
        >
          Docs for This Template
        </a>
      </li>
    </ul>
    <h2>Ecosystem</h2>
    <ul>
      <li>
        <a
          href="http://router.vuejs.org/"
          target="_blank"
        >
          vue-router
        </a>
      </li>
      <li>
        <a
          href="http://vuex.vuejs.org/"
          target="_blank"
        >
          vuex
        </a>
      </li>
      <li>
        <a
          href="http://vue-loader.vuejs.org/"
          target="_blank"
        >
          vue-loader
        </a>
      </li>
      <li>
        <a
          href="https://github.com/vuejs/awesome-vue"
          target="_blank"
        >
          awesome-vue{{countTest}}{{count}}
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
export default {
  name: 'HelloWorld',
  props: ['id'],
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      localCount: 2
    }
  },
  mounted () {
    // this.test()
    this.increment()
    console.log(this.id)
  },
  computed: {
    ...mapState('test', {
      countTest: state => state.count
    }),
    ...mapGetters('test', ['doneTodos', 'doneTodosCount']),
    count () {
      return this.$store.state.count
    }
  },
  methods: {
    ...mapMutations('test', {
      add: 'SOME_MUTATION'
    }),
    ...mapActions('test', {
      addAsync: 'incrementAsync',
      test: 'test'
    }),
    increment () {
      this.$store.commit('SOME_MUTATION', {
        amount: 10
      })
      this.$store.dispatch('incrementAsync', {
        amount: -19
      })
      console.log(this.$store.state.count)
      console.log(this.$store.getters.doneTodos)
      console.log(this.$store.getters.doneTodosCount)
      // modules
      this.add({amount: 100})
      this.addAsync({amount: -900})
      this.test()
      console.log('test20', this.count)
      console.log('test', this.doneTodos)
      console.log('test', this.doneTodosCount)
    },
    test () {
      axios.post('/api/test', {
      })
        .then(function (response) {
          // console.log(response)
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



